
/**
 *  (1-4)    .
 * @type { (number: string) => string | undefined } 
 */
export function to_circled_number(number) {
    switch (number) {
        case '1':
            return '';
        case '2':
            return '';
        case '3':
            return '';
        case '4':
            return '';
        default:
            return undefined;
    }
}